.error_page {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin-top: 3rem;
  padding: 1rem;

  &_title {
    font-size: 250px;
    margin: 0;
  }

  &_link {
    margin-top: 3rem;
    margin-bottom: 1.7rem;
    text-decoration: underline;
  }
}

@media (max-width: 768px) {
  .error_page {
    &_title {
      font-size: 160px !important;
    }

    &_link {
      margin-top: 5rem !important;
    }
  }
}
