$primary-color: #ff5656;

$bg-clr: #fff;

$bg-content: #f0f0f0;

@keyframes slide {
  0% {
    overflow: hidden;
    max-height: 0%;
    opacity: 0;
  }

  5% {
    opacity: 1;
  }

  50% {
    max-height: 50%;
  }

  100% {
    max-height: 100%;
  }
}

.svg-inline--fa {
  padding-right: 10px;
}

.collapse {
  &_header {
    z-index: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: $primary-color;
    color: white;
    border-radius: 0.3rem;
    margin-bottom: 1rem;
    box-shadow: 0 0 10px hsla(0, 0%, 0%, 0.1);
    cursor: pointer;

    &_title {
      padding: 0.4rem 0.5rem;
      margin: 0;
      font-size: 18px;
    }

    &_icon {
      padding: 0.4rem 0.5rem;
      font-size: 20px;
    }
  }

  &_content {
    position: relative;
    z-index: -1;
    padding: 0.8rem 1rem;
    background: $bg-content;
    margin-top: -1.2rem;
    margin-bottom: 1rem;
    border-radius: 0 0 0.3rem 0.3rem;
    font-size: 16px;
    line-height: 23px;
    animation: slide 1s ease-in;
  }
}

@media (max-width: 768px) {
  .collapse_content {
    animation: slide 2s ease-in-out;
  }
}
